.MovieCastImg {
  border-radius: 4px;
  width: 100%;
  object-fit: cover;
}

.ActorCard {
  display: grid;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  max-width: 200px;
  padding: 8px;
  border-radius: 8px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.ActorName {
  font-size: 18px;
  color: #294652;
}

.ActorCharacter {
  font-size: 14px;
  font-weight: 400;
}

.NoInfo {
  font-size: 16px;
  font-weight: 400;
  color: #010101;
}
